import { withStyles } from "@material-ui/core/styles";
import InfoIcon from "@mui/icons-material/Info";
import PrintIcon from "@mui/icons-material/Print";
import SettingsIcon from "@mui/icons-material/Settings";
import { IconButton } from "@mui/material";
import { Grid } from "@mui/material";
import { Tooltip } from "@mui/material";
import SettingsModal from "SoapHelper/SoapLabels/SettingsModal";
import Label from "SoapHelper/SoapLabels/labelLayouts/Label";
import { useSoapLabels, useSettings } from "SoapHelper/SoapLabels/store/actions";
import {
	rightAbsoluteContainer,
	secondTitle,
	wrapperForAbsolute,
	noPrint,
} from "SoapHelper/styles/styles";
import * as React from "react";

const commonGridPrint = {
	display: "block",
	position: "absolute",
	margin: "0 auto",
	textAlign: "center",
	fontSize: "10px",
	color: "#888",

	"@media print": {
		display: "none",
	},
};

const styles = () => ({
	rightAbsoluteContainer,
	secondTitle,
	wrapperForAbsolute: {
		...wrapperForAbsolute,
		...noPrint,
	},
	gridPrintLabels: {
		boxShadow: "rgb(100 100 111 / 50%) 2px 2px 7px 0px",
		display: "block",
		position: "relative",
		"@media print": {
			boxShadow: "none",
			borderWidth: "0 !important",
			borderWidth: "0 !important",
			verticalAlign: "top",
			width: "100%",
		},
	},
	gridPrintRow: {
		display: "flex",
		alignItems: "flex-start",
		"@media print": {
			pageBreakInside: "avoid",
			breakInside: "avoid",
		},
	},
	gridPrintWidth: {
		...commonGridPrint,
	},
	gridPrintHeight: {
		...commonGridPrint,
		textOrientation: "mixed",
		"-webkit-writing-mode": "vertical-lr",
		transform: "rotate(180deg)",
	},
});

const LabelsPrintView = (props) => {
	const { classes, forPrint } = props;
	const [settingsModalOpen, setSettingsModalOpen] = React.useState(false);
	const [pagePadding, setPagePadding] = React.useState(null);
	const [pagePaddingRaw, setPagePaddingRaw] = React.useState(null);

	const gridRef = React.useRef();

	const soapLabels = useSoapLabels();
	const settings = useSettings();

	const [layoutNbPerRow /*, setLayoutNbPerRow*/] = React.useState(settings.layoutNbPerRow);
	const [layout /*, setLayoutNbPerRow*/] = React.useState(settings.layout);
	const [dimensions, setDimensions] = React.useState({ width: 0, height: 0 });

	// Settings Modal
	const handleClickOpenSettings = () => {
		setSettingsModalOpen(true);
	};
	const handleSettingsModalClose = () => {
		setSettingsModalOpen(false);
	};

	const renderLabels = (_soapLabels, _layoutNbPerRow, _layout) => {
		if (_layout === "wide") {
			_layoutNbPerRow = 1;
		}

		let labels = [];
		_soapLabels.forEach((soapLabel) => {
			for (let i = 0; i < soapLabel.quantity; i++) {
				labels.push(soapLabel);
			}
		});

		const rows = labels.reduceRight(
			(r, i, _, s) => (r.push(s.splice(0, _layoutNbPerRow)), r),
			[]
		);

		return rows.map((row, r) => {
			return (
				<div className={classes.gridPrintRow} key={`labelPreviewRow-${r}`}>
					{row.map((label, l) => {
						const index = r * _layoutNbPerRow + l;
						return <Label key={`labelPreview-${index}`} soapLabel={label} />;
					})}
				</div>
			);
		});
	};

	// Print
	const handleClickPrint = () => {
		window.print();
	};

	// const isColumnLayout = layout === "columns";
	// const pagePadding = {
	// 	pt: Math.max(0, settings.pagePadding.pt - settings.padding.pt) + "px",
	// 	pb: Math.max(0, settings.pagePadding.pt - settings.padding.pb) + "px",
	// 	pl: Math.max(0, settings.pagePadding.pl) + "px",
	// 	pr: Math.max(0, settings.pagePadding.pl) + "px",
	// };

	React.useEffect(() => {
		if (settings) {
			setPagePaddingRaw({
				pt: Math.max(0, settings.pagePadding.pt - settings.padding.pt),
				pb: Math.max(0, settings.pagePadding.pt - settings.padding.pb),
				pl: Math.max(0, settings.pagePadding.pl),
				pr: Math.max(0, settings.pagePadding.pl),
			});
			setPagePadding({
				pt: Math.max(0, settings.pagePadding.pt - settings.padding.pt) + "px",
				pb: Math.max(0, settings.pagePadding.pt - settings.padding.pb) + "px",
				pl: Math.max(0, settings.pagePadding.pl) + "px",
				pr: Math.max(0, settings.pagePadding.pl) + "px",
			});
		}
	}, [settings]);

	React.useLayoutEffect(() => {
		if (pagePaddingRaw && gridRef.current) {
			console.log(pagePaddingRaw, gridRef.current.offsetWidth);
			setDimensions({
				// width: gridRef.current.offsetWidth,
				// height: gridRef.current.offsetHeight,
				width: gridRef.current.offsetWidth - (pagePaddingRaw.pl + pagePaddingRaw.pr),
				height: gridRef.current.offsetHeight - (pagePaddingRaw.pt + pagePaddingRaw.pb),
			});
		}
	}, [pagePaddingRaw]);

	return (
		<React.Fragment>
			<SettingsModal
				settingsModalOpen={settingsModalOpen}
				onCloseSettingsModal={handleSettingsModalClose}
			/>
			<div className={classes.wrapperForAbsolute}>
				<h2 className={classes.secondTitle}>
					Soap Labels Print Preview
					<Tooltip
						title="Lines are for visual aids only. And margins could render differently in your print settings."
						placement="right"
					>
						<IconButton size="small">
							<InfoIcon />
						</IconButton>
					</Tooltip>
				</h2>
				<div className={classes.rightAbsoluteContainer}>
					<IconButton onClick={handleClickPrint}>
						<PrintIcon />
					</IconButton>
					<IconButton onClick={handleClickOpenSettings}>
						<SettingsIcon />
					</IconButton>
				</div>
			</div>
			<Grid
				item
				className={classes.gridPrintLabels}
				{...pagePadding}
				{...(forPrint ? null : { ref: gridRef })}
			>
				<div
					className={classes.gridPrintWidth}
					style={{
						width: dimensions.width,
						// height: pagePadding ? pagePadding.pt : 0,
						top: pagePaddingRaw ? pagePaddingRaw.pt - 16 : 0,
					}}
				>
					← &nbsp; {dimensions.width}px &nbsp; →
				</div>
				<div
					className={classes.gridPrintHeight}
					style={{
						height: dimensions.height,
						left: pagePaddingRaw ? pagePaddingRaw.pl - 16 : 0,
					}}
				>
					← &nbsp; {dimensions.height}px &nbsp; →
				</div>
				{soapLabels && soapLabels.length
					? renderLabels(soapLabels, settings.layoutNbPerRow, settings.layout)
					: null}
			</Grid>
		</React.Fragment>
	);
};

export default withStyles(styles, { name: "LabelsPrintView" })(LabelsPrintView);
